  flex-direction: column;
  gap: 3.2rem;
  justify-content: space-between;
  width: 136rem;
}
.product-reviews__review-item-title {
  position: relative;
  z-index: 2;
}
.product-reviews__review-item-title span::before {
  height: 3.6rem;
}
