  margin-top: 2.4rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.product-reviews__button::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.1rem;
  margin-top: 0.2rem;
  background-color: #0e1c15;
  transition: width 0.3s;
}
.product-reviews__button:hover:after {
  width: 0%;
}

