const cartCheckboxes = () => {
    //   DOM
    const selectAllCheckbox = document.querySelector('.cart__heading-selectAll'); //  
    const deleteSelectedButton = document.querySelector('.cart__heading-delete'); //  
    const checkboxes = document.querySelectorAll('.cart__list .cart__checkbox-input'); //    cart__list

    //       " "
    function selectAllHandler() {
        checkboxes.forEach((checkbox) => {
            checkbox.checked = selectAllCheckbox.checked;
        });
    }

    //       " "
    function deleteSelectedHandler() {
        checkboxes.forEach((checkbox) => {
            if (checkbox.checked) {
                const cartElement = checkbox.closest('.cart__element');
                if (cartElement) {
                    cartElement.remove();
                }
            }
        });
    }

    //   
    selectAllCheckbox.addEventListener('change', selectAllHandler);
    deleteSelectedButton.addEventListener('click', deleteSelectedHandler);
};

export default cartCheckboxes;
